.home {
    h2 {
        font-size: 48px;
        line-height: 72px;
        font-weight: 700;
        margin-bottom: 60px;
        @include pad {
            font-size: 32px;
            line-height: 48px;
            margin-bottom: 32px;
        }
        @include mobile {
            font-size: 24px;
            line-height: 48px;
            margin-bottom: 32px;
        }
    }
}
.banner {
    padding: 346px 0 344px 0;
    background-image: url(../images/Home/68@2x.png);
    background-size: cover;
    background-position: center center;
    .container {
        margin-right: 18.75%;
        @include pad {
            margin-right: 48px;
        }
    }
    @include mobile {
        padding: 163px 0 164px 0;
        .fz-9 {
            font-size: 16px;
            line-height: 30px;
        }
        .fz-16 {
            font-size: 32px;
            line-height: 72px;
        }
        .container {
            margin-right: 12px;
        }
    }
}
.professional,
.classic,
.design {
    padding: 64px 0 80px 0;
}
.professional {
    ul {
        flex-wrap: wrap;
        justify-content: center;
    }
    li {
        width: 23.5%;
        margin-right: 2%;
        padding: 0 37px;
    }
    li:last-child {
        margin-right: 0;
    }
    img {
        max-width: 100px;
        margin-bottom: 36px;
    }
    li:nth-child(3) img {
        margin-bottom: 42px;
    }
    li:last-child img {
        margin-bottom: 66px;
    }
    p {
        color: #000;
        text-align: left;
    }
    @include pad {
        img {
            max-width: 80px;
            margin-bottom: 12px;
        }
        li:nth-child(3) img {
            margin-bottom: 14px;
        }
        h3 {
            font-size: 20px;
            line-height: 30px;
        }
        li {
            width: 42%;
            margin-right: 0%;
            padding: 0 50px;
        }
        li:nth-child(3),
        li:last-child {
            margin-bottom: 0;
        }
        li:nth-child(1) {
            order: 3;
            margin-right: 2%;
        }
        li:nth-child(2) {
            order: 1;
            margin-right: 2%;
            margin-bottom: 40px;
        }
        li:nth-child(3) {
            order: 2;
            margin-bottom: 52px;
        }
        li:nth-child(4) {
            order: 4;
            img {
                margin-bottom: 32px;
            }
        }
    }
    @include mobile {
        padding: 32px 0 56px;
        img {
            margin-bottom: 16px;
        }
        .fz-12 {
            font-size: 24px;
            line-height: 48px;
        }
        ul {
            flex-wrap: wrap;
        }
        li:nth-child(1),
        li:nth-child(2),
        li:nth-child(3),
        li:nth-child(4) {
            padding: 0;
            min-width: 234px;
            margin-right: 0% !important;
            margin-bottom: 28px !important;
        }
    }
}

.classic {
    li {
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 32px;
        p{
            align-items: center;
        }
    }
    li:last-child {
        margin-bottom: 0;
    }
    img {
        max-width: 792px;
        margin-right: 24px;
    }
    li:nth-child(2) {
        flex-direction: row-reverse;
        img {
            margin-right: 0px;
        }
        p {
            margin-right: 24px;
        }
    }
    i {
        display: inline-block;
        width: 48px;
        margin-right: 8px;
    }
    @include pad {
        padding: 40px 60px 40px 48px;
        li {
            flex-wrap: nowrap;
            justify-content: center;
        }
        li:nth-child(1) > img {
            margin-right: 91px;
        }
        li:nth-child(2) {
            flex-direction: row;
            p {
                margin-right: 0px;
            }
        }
        li:nth-child(2) > img,
        li:nth-child(3) > img {
            margin-right: 35px;
        }
        li:nth-child(3) {
            i {
                max-width: 23px;
            }
        }
        img {
            max-width: 410.85px;
            margin-right: 8px;
        }
        p {
            font-size: 28px;
            margin-right: 0px;
        }
        i {
            max-width: 32px;
            margin-right: 8px;
            vertical-align: bottom;
        }
    }
    @include mobile {
        padding: 24px 11px 24px 12px;
        h2 {
            margin-bottom: 16px;
        }
        li {
            margin-bottom: 8px;
            p {
                filter: brightness(500);
                display: flex;
                justify-content: center;
                max-width: 198px;
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
            }
            img {
                min-width: 100%;
            }
        }

        li:nth-child(1),
        li:nth-child(2),
        li:nth-child(3) {
            p {
                margin-right: 0px;
            }
            img {
                margin-right: 0px;
            }
        }
        li:nth-child(2) {
            p {
                img {
                    width: 32px;
                    margin-right: 7.5px;
                }
            }
        }
        li:nth-child(3) {
            p {
                img {
                    width: 23px;
                    margin-right: 8px;
                }
            }
        }
    }
}
.design {
    background-image: url(../images/Home/nick-owuor-astro-nic-visuals-54Q_HvMWtVE-unsplash@2x.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center -280px;
    @include pad {
        background-position: center -618px;
    }
    .waterfall {
        max-width: 1539px;
        margin-bottom: 60px;
        overflow: hidden;
        img {
            object-fit: cover;
        }
        .waterfallLeft {
            max-width: 573px;
            margin-right: 8px;
            img {
                height: 100%;
            }
            @include pad {
                margin-right: 3px;
            }
            @include mobile {
                margin-right: 2px;
            }
        }
        .waterfallRight {
            max-width: 958px;
            > img:first-child {
                height: 54%;
                margin-bottom: 8px;
                @include pad {
                    margin-bottom: 4px;
                }
                @include mobile {
                    height: 35.8%;
                    margin-bottom: 2px;
                }
            }
        }
        .waterfallRightBottom {
            height: 48%;
            .boy {
                width: 13%;
                height: 100%;
                margin-right: 8px;
                @include pad {
                    margin-right: 3px;
                }
                @include mobile {
                    margin-right: 2px;
                    margin-top: 2px;
                }
            }
            .waterfallRightBottomR {
                width: 86%;
            }
            .waterfallRightBottomRUnder {
                height: 43.9%;
                justify-content: flex-end;
                align-items: center;
            }
            .more {
                background-color: #facc00;
                height: 100%;
                align-items: center;
                padding-left: 27px;
                @include pad {
                    padding-left: 10px;
                }
                span {
                    font-size: 4rem;
                    background-color: #fff;
                    line-height: 105px;
                    padding-left: 37px;
                    font-weight: 700;
                    transition: 1s;
                    &:hover {
                        text-shadow: 0 -100px 0 #aa0701dc, 0 -150px 0 #aa070177;
                    }
                    @include pad {
                        font-size: 40px;
                        line-height: 59px;
                        padding-left: 19px;
                        &:hover {
                            text-shadow: 0 -46px 0 #aa0701dc, 0 -96px 0 #aa070177;
                        }
                    }
                    @include mobile {
                        font-size: 26px;
                        line-height: 41px;
                        padding-left: 19px;
                        &:hover {
                            text-shadow: 0 -46px 0 #aa0701dc, 0 -96px 0 #aa070177;
                        }
                    }
                }
                margin-right: 84px;
                @include pad {
                    margin-right: 48px;
                }
            }
            .arrowBlock {
                background-color: #fff;
                width: 84px;
                height: 105px;
                right: -84px;
                @include pad {
                    width: 48px;
                    height: 59px;
                    right: -48px;
                }
                @include mobile {
                    width: 32px;
                    height: 41px;
                    right: -32px;
                }
            }
            .arrow {
                width: 84px;
                height: 105px;
                padding: 29px 10px;
                transition: 1s;
                @include pad {
                    width: 48px;
                    height: 59px;
                    padding: 16.3px 10px;
                }
                @include mobile {
                    width: 32px;
                    height: 41px;
                    padding: 12px 14px 11px 0;
                }
            }
            .more:hover .arrow {
                transform: translateX(5px);
            }
            @include mobile {
                position: relative;
                flex-direction: column-reverse;
                margin-top: 26%;
                height: 48%;
                .waterfallRightBottomR {
                    width: 100%;
                }
                .waterfallRightBottomRUnder {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 20%;
                }
                .boy {
                    width: 19%;
                    height: 100%;
                }
            }
        }
        @include mobile {
            margin-bottom: 20px;
            padding-left: 13px;
            padding-right: 12px;
        }
    }
    .dobule {
        margin-bottom: 144px;
        li:first-child {
            margin-right: 30px;
        }
        li {
            max-width: 585px;
            &::after {
                content: "";
                position: absolute;
                background-color: #fff;
                display: block;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                opacity: 0.5;
            }
            span {
                white-space: nowrap;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 2;
                font-weight: 700;
                @include pad {
                    font-size: 32px;
                    line-height: 45px;
                }
            }
        }
        @include pad {
            margin-bottom: 40px;
        }
        @include mobile {
            margin-bottom: 24px;
            flex-direction: column;
            li:first-child {
                margin-right: 0px;
                margin-bottom: 20px;
            }
            li {
                max-width: none;
            }
        }
    }
    .customer {
        ul {
            justify-content: space-between;
        }
        li {
            max-width: 282px;
            background-color: #fff;
            border: 1px solid #dee2e6;
        }
        img {
            margin-bottom: 16px;
        }
        p {
            padding-left: 16px;
            margin-bottom: 16px;
        }
        p:nth-child(2) {
            font-weight: 750;
        }
        p:last-child {
            color: #707070;
            margin-bottom: 16px;
        }
        @include pad {
            .fz-4-m {
                font-weight: 500;
            }
            ul {
                flex-wrap: wrap;
            }
            li {
                max-width: 324px;
            }
            li:nth-child(1),
            li:nth-child(2) {
                margin-bottom: 24px;
            }
            h2 {
                line-height: 36px;
            }
        }
        @include mobile {
            li {
                max-width: none;
                margin-bottom: 16px;
                width: 100%;
            }
            li:last-child {
                margin-bottom: 0;
            }
            h2 {
                margin-bottom: 16px;
                line-height: 36px;
            }
        }
    }
    @include pad {
        padding: 40px 48px;
    }
    @include mobile {
        padding: 24px 11px 24px 12px;
        h2 {
            margin-bottom: 16px;
            line-height: 36px;
        }
    }
}

.content {
    padding: 64px 0 92px 0;
    .container {
        max-width: 792px;
    }

    h2 {
        margin-bottom: 24px;
    }
    p {
        margin-bottom: 20px;
    }
    label {
        span {
            margin-bottom: 4px;
        }
        font-weight: 600;
    }
    textarea,
    input:not([type="checkbox"]) {
        margin-bottom: 16px;
    }
    textarea {
        background-color: transparent;
        resize: none;
        width: 100%;
        max-height: 136px;
        border: 1px solid #000;
        text-indent: 16px;
        line-height: 32px;
    }
    button[type="submit"] {
        &:hover {
            background-color: #650300;
        }
    }
    input:not([type="checkbox"]) {
        width: 100%;
        height: 50px;
        text-indent: 16px;
    }
    input[type="checkbox"] {
        width: 20px;
        height: 20px;
        border-radius: 2.5px;
        border: 2px solid #000;
        margin-right: 8px;
        cursor: pointer;
    }
    input:checked {
        background: #000;
    }
    button {
        display: block;
        width: 180px;
        height: 54px;
        border: 0;
        cursor: pointer;
    }
    @include pad {
        padding: 40px 135px 56px 135px;
        p {
            font-size: 16px;
            line-height: 24px;
            font-weight: 900;
            margin-bottom: 16px;
        }
        h2 {
            margin-bottom: 16px;
        }
        button {
            width: 136px;
            height: 36px;
        }
    }
    @include mobile {
        padding: 24px 24px 48px;
        p {
            font-size: 16px;
            line-height: 24px;
            font-weight: 900;
            margin-bottom: 8px;
        }
        h2 {
            margin-bottom: 8px;
        }
        input:not([type="checkbox"]) {
            height: 36px;
            margin-bottom: 12px;
        }
        input[type="checkbox"] {
            width: 15px;
            height: 15px;
            margin-right: 2px;
            margin-top: 4px;
        }
        label[for="agree"] {
            font-size: 14px;
            line-height: 21px;
            margin-bottom: 20px;
        }
        textarea {
            margin-bottom: 12px;
            max-height: 100px;
        }
        button {
            width: 136px;
            height: 36px;
        }
    }
    @media (max-width: 500px) {
        input[type="checkbox"] {
            width: 15px;
            height: 13px;
            margin-right: 2px;
            margin-top: 4px;
        }
    }
}
