footer {
    padding: 48px 0 27.43px 0;
    .container{
        max-width: 1150px;
    }
    img {
        max-width: 48px;
    }
    .tel {
        max-width: 36px;
        margin-right: 23px;
    }
    .email {
        margin-right: 12px;
    }
    .footerHeader,
    .copyright {
        justify-content: space-between;
    }
    .footerHeader {
        margin-bottom: 18px;
    }
    .menu {
        li {
            margin-right: 48px;
        }
    }
    .icon {
        li {
            width: 48px;
        }
        li:not(:last-child) {
            margin-right: 32px;
        }
    }
    .copyright {
        align-items: center;
        img {
            max-width: 80px;
            margin-right: 38px;
        }
        p:last-child {
            span:first-child {
                margin-right: 62px;
            }
        }
    }
    @include pad {
        padding: 40px 48px;
        ul {
            margin-bottom: 24px;
        }
        .fz-9 {
            font-size: 20px;
            line-height: 30px;
            margin-bottom: 11px;
            img {
                width: 23px;
                margin-right: 12px;
            }
        }
        .fz-9 + .fz-9 {
            margin-bottom: 27px;
            margin-right: 8px;
        }
        .menu {
            li {
                margin-right: 40px;
            }
        }
        .icon {
            li {
                width: 24px;
            }
            li:not(:last-child) {
                margin-right: 24px;
            }
        }
        .copyright {
            align-items: center;
            img {
                display: none;
            }
            p:last-child {
                span:first-child {
                    margin-right: 24px;
                }
            }
        }
    }
    @include mobile {
        position: relative;
        padding: 20px 11px;
        .menu {
            display: none;
        }
        .footerHeader {
            position: absolute;
            right: 12px;
        }
        .fz-9 {
            font-size: 16px;
            line-height: 30px;
            margin-bottom: 8px;
            img {
                width: 13px;
                margin-right: 8px;
            }
        }
        .fz-9 + .fz-9 {
            margin-bottom: 24px;
            margin-right: 5px;
            img {
                width: 20px;
                height: 20px;
            }
        }
        hr {
            margin-bottom: 16px;
        }
        .copyright {
            display: block;
            p {
                font-size: 14px;
                line-height: 21px;
                margin-bottom: 8px;
            }
            span {
                display: block;
            }
            span:first-child {
                margin-bottom: 8px;
            }
            img {
                margin-right: 0px;
                max-width: 46px;
                display: block;
                position: absolute;
                right: 12px;
                bottom: 20px;
            }
        }
    }
}
