.bannerProblem {
    padding-top: 214px;
    background-image: url("../images/FAQ/72@2x.png");
    @include pad {
        background-image: url("../images/FAQ/72-2@2x.png");
    }
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    h2 {
        font-size: 64px;
        line-height: 90px;
        margin-right: 10.1px;
        margin-left: 40px;
    }
    img {
        max-width: 57.9px;
    }
    @include pad {
        padding-top: 237px;
        h2 {
            font-size: 48px;
            line-height: 80px;
            margin-right: 16px;
        }
        img {
            max-width: 48px;
        }
    }
    @include mobile {
        padding-top: 103px;
        padding-bottom: 8px;
        h2 {
            font-size: 28px;
            line-height: 28px;
            margin-left: 11px;
        }
        img {
            max-width: 24px;
        }
    }
}
.qa {
    padding: 88px 0 156px 0;
    @include pad {
        padding: 40px 48px 104px 48px;
    }
    @include mobile {
        padding: 24px 11px 56px 12px;
    }
    > h2 {
        font-size: 32px;
        line-height: 63px;
        margin-bottom: 16px;
        @include pad {
            font-size: 28px;
            line-height: 28px;
            margin-bottom: 32px;
        }
        @include mobile {
            font-size: 20px;
            line-height: 28px;
            margin-bottom: 16px;
        }
    }
    li {
        border: 1px solid #000;
        border-top: 0px;
        justify-content: space-between;
        cursor: pointer;
        &.active {
            p {
                padding: 22px 39.75px 22px 38px;
                height: auto;
                @include pad {
                    padding: 16px 0px 16px 24px;
                }
                @include mobile {
                    padding: 16px 0px 16px 16px;
                }
            }
            h2 {
                &::after {
                    content: "";
                    transform: rotate(150deg) translateY(-50%);
                }
                &::before {
                    content: "";
                    transform: rotate(-150deg) translateY(-50%);
                }
            }
        }
        h2 {
            position: relative;
            font-size: 20px;
            line-height: 23px;
            padding: 0 39.75px 0 38px;
            font-weight: 700;
            padding: 22px 39.75px 22px 38px;
            &::after {
                content: "";
                position: absolute;
                right: 20px;
                border: 1px solid #000;
                background-color: #000;
                width: 15px;
                top: 50%;
                transform: rotate(30deg) translateY(-50%);
                transition: 0.5s;
            }
            &::before {
                content: "";
                position: absolute;
                right: 32px;
                border: 1px solid #000;
                background-color: #000;
                width: 15px;
                top: 50%;
                transform: rotate(-30deg) translateY(-50%);
                transition: 0.5s;
            }
            @include pad {
                padding: 16px 0px 16px 24px;
                font-size: 16px;
                line-height: 24px;
                &::after {
                    content: "";
                    right: 24px;
                    width: 10px;
                }
                &::before {
                    content: "";
                    right: 32px;
                    width: 10px;
                }
            }
            @include mobile {
                padding: 16px 0px 16px 16px;
                font-size: 16px;
                line-height: 24px;
                &::after {
                    content: "";
                    right: 24px;
                    width: 10px;
                }
                &::before {
                    content: "";
                    right: 32px;
                    width: 10px;
                }
            }
        }
        p {
            font-size: 16px;
            line-height: 24px;
            background-color: #f2f2f2;
            font-weight: 500;
            overflow: hidden;
            height: 0;
            padding-left: 22px;
            transition: 0.5s;
            @include pad {
                font-size: 14px;
                line-height: 21px;
            }
        }
    }
    li:first-child {
        border-top: 1px solid #000;
    }
}
