.hamburgerMenu {
    position: fixed;
    z-index: 999;
    top: 0;
    left: -100%;
    width: 100%;
    text-align: center;
    font-weight: 400;
    transition: left 1s;
    a {
        display: block;
    }
    img {
        max-width: 48px;
    }
    li:first-child {
        height: 50px;
        line-height: 50px;
        border-bottom: 1px solid #fff;
        border-top: none;
        border-right: none;
        border-left: none;
    }
    li {
        height: 60px;
        border-top: 1px solid #fff;
        border-right: 1px solid #fff;
        border-left: 1px solid #fff;
        line-height: 60px;
    }
    li:last-child {
        border-bottom: 1px solid #fff;
    }
    .menu {
        display: block;
        top: 13px;
        left: 16px;
        max-width: 24px;
        cursor: pointer;
    }
}

header {
    justify-content: space-between;
    align-items: center;
    padding: 30px 4.58%;
    a {
        display: block;
        padding: 10px;
    }
    h1 {
        max-width: 80px;
    }
    li {
        margin-right: 44px;
        font-size: 20px;
    }
    li:last-child {
        margin-right: 0;
    }
    .mobileNav {
        display: none;
    }
    @include mobile {
        nav,
        h1 {
            display: none;
        }
        position: relative;
        height: 50px;
        padding: 0;
        .mobileNav {
            width: 100%;
            display: block;
            cursor: pointer;
        }
        .logo {
            max-width: 48px;
        }
        .menu {
            max-width: 24px;
            left: 16px;
            top: 13px;
        }
        li {
            margin-right: 40px;
        }
    }
}
