.breadcrumb {
    > ul {
        justify-content: space-between;
        width: 100%;
        line-height: 54px;
    }
    img {
        max-width: 24px;
        cursor: pointer;
    }
    .breadcrumbMenu {
        li {
            margin-right: 48px;
            @include pad {
                margin-right: 32px;
            }
        }
    }
    @include pad {
        padding: 0 48px;
        a {
            font-size: 16px;
        }
    }
    @include mobile {
        display: none;
    }
}
.blogList {  
    padding: 88px 0;
    .padMore {
        display: none;
    }
    @include pad {
        padding: 40px 48px 52px;
    }
    @include mobile {
        padding: 24px 12px 40px 11px;
    }
    h3 {
        font-size: 32px;
        line-height: 45px;
    }
    li {
        justify-content: space-between;
        margin-bottom: 60px;
        &:last-child {
            margin-bottom: 0px;
        }
        @include mobile {
            display: block;
        }
    }
    .blogListLeft {
        display: block;
        width: 49%;
        @include mobile {
            margin-bottom: 8px;
            width: 100%;
        }
        img {
            object-fit: cover;
        }
    }
    .blogListRight {
        width: 48.75%;
        p {
            font-size: 20px;
            line-height: 30px;
        }
        p:nth-child(2) {
            justify-content: space-between;
            margin-bottom: 40px;
            span:last-child {
                font-size: 16px;
            }
        }
        .blogListContent {
            color: #555555;
        }
        p:last-child {
            a {
                color: #000;
            }
            text-align: right;
            font-size: 36px;
            line-height: 54px;
            font-weight: 700;
            cursor: pointer;
            span {
                margin-right: 8px;
            }
            img {
                max-width: 24px;
                vertical-align: baseline;
            }
        }
        @include mobile {
            width: 100%;
            .blogListContent {
                display: none;
            }
        }
    }
    @include pad {
        .padMore {
            display: inline;
            font-weight: 600;
            color: #000;
        }
        h3 {
            font-size: 20px;
            line-height: 28px;
        }
        li {
            margin-bottom: 32px;
        }
        .blogListRight {
            p {
                font-size: 16px;
            }
            p:nth-child(2) {
                margin-bottom: 16px;
                span:last-child {
                    font-size: 14px;
                }
            }
            .blogListContent {
                font-size: 14px;
                line-height: 21px;
            }
            p:last-child {
                display: none;
            }
        }
    }
    @include mobile {
        h3 {
            font-size: 16px;
            line-height: 22px;
        }
        li {
            margin-bottom: 24px;
        }
        .blogListRight {
            p {
                font-size: 14px;
            }
            p:nth-child(2) {
                margin-bottom: 0px;
                span {
                    font-weight: 600;
                }
            }
        }
    }
}
.blogListPageNavigation {
    justify-content: center;
    margin-bottom: 156px;
    font-size: 19px;
    li {
        padding: 0px 17px 0px 14px;
        line-height: 48px;
        border-top: 1px solid #555555;
        border-bottom: 1px solid #555555;
        border-left: 1px solid #555555;
        &:hover {
            background-color: #000;
            color: #fff;
        }
        cursor: pointer;
    }
    .active {
        background-color: #000;
        color: #fff;
    }
    li:last-child {
        border-right: 1px solid #555555;
    }

    li:last-child,
    li:first-child {
        padding: 0px 15px 0px 9px;
        &:hover {
            background-color: #f2f2f2;
            color: #000;
        }
    }
    @include pad {
        font-size: 18px;
        margin-bottom: 104px;
    }
    @include mobile {
        font-size: 11px;
        margin-bottom: 56px;
        li {
            padding: 0px 10px 0px 11px;
            line-height: 32px;
        }
        li:last-child,
        li:first-child {
            padding: 0px 8px 0px 9px;
            &:hover {
                background-color: #f2f2f2;
                color: #000;
            }
        }
    }
}
.breadcrumbMobile {
    display: none;
}
@include mobile {
    .breadcrumbMobile {
        display: block;
        a {
            color: #000;
        }
        li {
            display: none;
        }
        li:last-child {
            cursor: pointer;
            display: block;
            line-height: 28px;
            border: 1px solid #000;
            .arrowUp {
                display: none;
            }
            img {
                max-width: 38.99px;
            }
        }
    }
    .breadcrumbMobile.active {
        position: absolute;
        background-color: #fff;
        left: 0;
        right: 0;
        li {
            display: block;
            border-bottom: 1px solid #00000020;
            line-height: 50px;
            font-weight: 900;
        }
        li:last-child {
            line-height: 28px;
            border: 1px solid #000;
        }
        li:last-child .arrowDown {
            display: none;
        }
        li:last-child .arrowUp {
            display: inline;
        }
    }
}
