body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial,
        sans-serif;
}
.primary-color-bg {
    background-color: #aa0601;
}
.primary-color {
    color: #aa0601;
    font-weight: 600;
}
.primary-color-dark {
    color: #650300;
    font-weight: 600;
}
.primary-color-dark-bg {
    background-color: #650300;
}
.white {
    color: #fff;
}
.pink-bg {
    background-color: #aa06010d;
}

.font-i {
    font-style: italic;
}
//---------------------------------
.d-flex {
    display: flex;
}
.flex-center {
    justify-content: center;
    align-items: center;
}
.m-auto {
    margin: auto;
}
.mr-auto {
    margin-right: auto;
}
.ml-auto {
    margin-left: auto;
}
.text-center {
    text-align: center;
}
.text-right {
    text-align: right;
}
.text-left {
    text-align-last: left;
}
.psa {
    position: absolute;
}
.psr {
    position: relative;
}
.pos-center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
//---------------------------------
.fz-m {
    font-size: 14px;
    line-height: 21px;
}
.fz-4-s {
    font-size: 16px;
    line-height: 21px;
}
.fz-4-m {
    font-size: 16px;
    line-height: 24px;
}
.fz-5 {
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
}
.fz-6 {
    font-size: 24px;
    line-height: 36px;
}
.fz-9 {
    font-size: 36px;
    line-height: 54px;
}
.fz-12 {
    font-size: 48px;
    line-height: 72px;
    font-weight: 700;
}
.fz-16 {
    font-size: 64px;
    line-height: 96px;
}
.fz-18 {
    font-size: 72px;
    line-height: 108px;
}
//---------------------------------
.container {
    max-width: 1200px;
    margin: 0 auto;
}

.mb-22 {
    margin-bottom: 22px;
}
.mb-24 {
    margin-bottom: 24px;
}
.mb-42 {
    margin-bottom: 42px;
}
