.bannerLocation {
    @include pad {
        .locationBannerIcon {
            max-width: 30.49px;
        }
    }
    @include mobile {
        .locationBannerIcon {
            max-width: 16.72px;
        }
    }
}

.locationsMap {
    justify-content: space-between;
    padding: 16px 0 156px 0;
    font-size: 24px;
    line-height: 30px;
    font-weight: 600;
    @include pad {
        padding: 8px 48px 104px 48px;
        font-size: 16px;
    }
    @include mobile {
        font-size: 14px;
        padding: 8px 11px 56px 12px;
    }
    .selectShop {
        justify-content: space-between;
        align-items: center;
        max-width: 384px;
        margin-bottom: 32px;
        @include pad {
            max-width: 324px;
            margin-bottom: 24px;
        }
        @include mobile {
            max-width: 100%;
            margin-bottom: 20px;
        }
    }
    select {
        height: 45px;
        width: 266px;
        padding-left: 19px;
        font-size: 18px;
        color: #495057;
        cursor: pointer;
        @include pad {
            width: 237px;
            height: 46px;
            font-size: 16px;
        }
        @include mobile {
            width: 79.5%;
            height: 36px;
            font-size: 14px;
        }
    }
    .locationsListShop {
        justify-content: space-between;
        .map {
            max-width: 792px;
        }
        @include pad {
            flex-direction: column;
            align-items: center;
        }
    }
    .shopDetail {
        max-width: 384px;
        li {
            border: 1px solid #00000020;
            margin-bottom: 28px;
        }
        .iconTel {
            max-width: 26px;
            margin-right: 8px;
        }
        .iconClock {
            max-width: 24px;
            margin-right: 9px;
        }
        .iconPin {
            max-width: 18px;
            margin-right: 12px;
        }
        h3 {
            padding-left: 24px;
            line-height: 65px;
            font-size: 24px;
            border-bottom: 1px solid #00000020;
            margin-bottom: 16px;
        }
        p {
            font-size: 20px;
            line-height: 28px;
            margin-bottom: 8px;
        }
        p:nth-child(2) {
            padding-left: 36px;
        }
        p:nth-child(3) {
            padding-left: 37px;
        }
        p:nth-child(4) {
            padding-left: 40px;
            margin-right: 14%;
            margin-bottom: 16px;
        }
        button {
            background-color: #000;
            font-size: 20px;
            line-height: 68px;
            color: #fff;
            border: none;
            width: 100%;
            cursor: pointer;
        }
        @include pad {
            display: flex;
            justify-content: space-between;
            align-items: center;
            max-width: 768px;
            margin-bottom: 32px;
            .shopContent {
                border: 1px solid #00000020;
                width: 48.5%;
            }
            img {
                width: 48.5%;
            }
            li {
                border: 1px solid #00000020;
                margin-bottom: 28px;
                padding: 9px 0;
            }
            .iconTel {
                max-width: 21px;
                margin-right: 13px;
            }
            .iconClock {
                max-width: 16px;
                margin-right: 16px;
            }
            .iconPin {
                max-width: 12px;
                margin-right: 18px;
            }
            h3 {
                padding-left: 16px;
                line-height: 38px;
                font-size: 16px;
                border-bottom: 1px solid #00000020;
            }
            p {
                font-size: 14px;
                line-height: 21px;
            }
            p:nth-child(2) {
                padding-left: 16px;
            }
            p:nth-child(3) {
                padding-left: 19px;
            }
            p:nth-child(4) {
                padding-left: 21px;
                margin-right: 0%;
            }
            button {
                display: none;
            }
        }
        @include mobile {
            > img {
                width: 100%;
            }
            flex-direction: column;
            .shopContent {
                width: 100%;
            }
        }
    }
}
