.bannerLocation {
    padding-top: 214px;
    background-image: url("../images/FAQ/72@2x.png");
    @include pad {
        background-image: url("../images/FAQ/72-2@2x.png");
    }
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    h2 {
        font-size: 64px;
        line-height: 90px;
        margin-right: 24px;
    }
    img {
        max-width: 40.33px;
    }
    @include pad {
        padding-top: 237px;
        h2 {
            font-size: 48px;
            line-height: 80px;
            margin-right: 16px;
            margin-left: 48px;
        }
        img {
            max-width: 30.49px;
        }
    }
    @include mobile {
        padding-top: 103px;
        padding-bottom: 8px;
        h2 {
            font-size: 28px;
            line-height: 28px;
            margin-left: 11px;
            margin-right: 8px;
        }
        img {
            max-width: 16.72px;
        }
    }
}
.locationsList {
    padding: 16px 0 154px 0;
    font-size: 24px;
    line-height: 30px;
    font-weight: 600;
    @include pad {
        padding: 8px 48px 104px 48px;
        font-size: 16px;
    }
    @include mobile {
        font-size: 14px;
        padding: 8px 11px 56px 12px;
    }
    select {
        height: 45px;
        width: 266px;
        padding-left: 19px;
        font-size: 18px;
        margin-bottom: 32px;
        color: #495057;
        margin-left: 22px;
        cursor: pointer;
        @include pad {
            width: 237px;
            margin-bottom: 24px;
            height: 46px;
            font-size: 16px;
            margin-left: 23px;
            padding-left: 16px;
        }
        @include mobile {
            width: 78.4%;
            margin-bottom: 20px;
            height: 36px;
            font-size: 14px;
            margin-left: 16px;
        }
    }
    .locationsListShop {
        justify-content: space-between;
        flex-wrap: wrap;
        li {
            border: 1px solid #00000020;
            width: 32.2%;
            margin-bottom: 28px;
            @include pad {
                width: 48.55%;
                margin-bottom: 24px;
            }
            @include mobile {
                width: 100%;
                margin-bottom: 20px;
            }
        }
        .iconTel {
            max-width: 26px;
            margin-right: 8px;
        }
        .iconClock {
            max-width: 24px;
            margin-right: 9px;
        }
        .iconPin {
            max-width: 18px;
            margin-right: 12px;
        }
        h3 {
            padding-left: 24px;
            line-height: 65px;
            font-size: 24px;
            font-weight: 600;
            border-bottom: 1px solid #00000020;
            margin-bottom: 16px;
        }
        p {
            font-size: 20px;
            line-height: 28px;
            margin-bottom: 8px;
        }
        p:nth-child(3) {
            padding-left: 36px;
        }
        p:nth-child(4) {
            padding-left: 37px;
        }
        p:nth-child(5) {
            padding-left: 40px;
            margin-right: 14%;
            margin-bottom: 16px;
        }
        @include pad {
            .iconTel {
                max-width: 21px;
                margin-right: 13.11px;
            }
            .iconClock {
                max-width: 16px;
                margin-right: 16px;
            }
            .iconPin {
                max-width: 12px;
                margin-right: 18px;
            }
            h3 {
                padding-left: 16px;
                line-height: 38px;
                font-size: 16px;
                border-bottom: 1px solid #00000020;
                margin-bottom: 16px;
            }
            p {
                font-size: 14px;
                line-height: 21px;
            }
            p:nth-child(3) {
                padding-left: 16px;
            }
            p:nth-child(4) {
                padding-left: 19px;
            }
            p:nth-child(5) {
                padding-left: 21px;
                margin-bottom: 16px;
                margin-right: 24px;
            }
        }
        button {
            background-color: #000;
            font-size: 20px;
            line-height: 68px;
            color: #fff;
            border: none;
            width: 100%;
            cursor: pointer;
            @include pad {
                font-size: 14px;
                line-height: 52px;
            }
        }
    }
}
