.productsMenu {
    li {
        justify-content: center;
        font-size: 42px;
        line-height: 63px;
        font-weight: 600;
        padding: 24px 0px;
        width: 33.3%;
        border-left: 1px solid #dcdcdc;
        border-right: 1px solid #dcdcdc;
        border-bottom: 1px solid #dcdcdc;
        a {
            color: #000;
        }
    }
    .active {
        padding: 24px 0 8px 0;
        border-bottom: 16px solid $primary-color;
    }
    img {
        filter: brightness(000);
        max-width: 48px;
        margin-right: 8px;
    }
    li:last-child img {
        max-width: 35px;
    }
    @include pad {
        li {
            font-size: 28px;
            line-height: 42px;
            padding: 16px 0px;
            cursor: pointer;
        }
        .active {
            padding: 16px 0 8px 0;
            border-bottom: 8px solid $primary-color;
        }
        img {
            max-width: 32px;
        }
        li:last-child img {
            max-width: 23px;
        }
    }
    @include mobile {
        li {
            align-items: center;
            font-size: 14px;
            line-height: 19px;
            padding: 8px 0px;
            cursor: pointer;
        }
        .active {
            padding: 8px 0 4px 0;
            border-bottom: 4px solid $primary-color;
        }
        img {
            max-width: 14.99px;
        }
        li:last-child img {
            max-width: 10.77px;
        }
    }
}
.productsBanner {
    margin-bottom: 88px;
    justify-content: space-between;
    img {
        max-width: 948px;
    }
    .mobileShow {
        display: none;
    }
    @include pad {
        justify-content: center;
        .computerShow {
            display: none;
        }
        .mobileShow {
            display: block;
        }
        margin-bottom: 40px;
        li{
            width: 50%;
            max-width: 384px;
        }
        img {
            object-fit: cover;
            height: 309px;
        }
    }
    @include mobile {
        margin-bottom: 24px;
        img {
            object-fit: cover;
            height: 144px;
        }
    }
}
.productsList {
    h2 {
        font-size: 42px;
        line-height: 63px;
        font-weight: 600;
    }
    h2 + p {
        font-size: 32px;
        line-height: 48px;
        margin-bottom: 48px;
    }
    @include pad {
        h2 {
            font-size: 32px;
            line-height: 48px;
        }
        h2 + p {
            font-size: 24px;
            line-height: 36px;
            margin-bottom: 32px;
        }
    }
    @include mobile {
        h2 {
            font-size: 20px;
            line-height: 48px;
        }
        h2 + p {
            font-size: 16px;
            line-height: 36px;
            margin-bottom: 20px;
        }
    }
}
.productsListDetails {
    flex-wrap: wrap;
    li {
        width: 23.5%;
        margin-bottom: 104px;
    }
    li:not(:nth-child(4n)) {
        margin-right: 2%;
    }
    p {
        justify-content: space-between;
        margin-bottom: 4px;
        font-size: 24px;
        line-height: 36px;
        font-weight: 600;
    }
    p:last-child {
        justify-content: flex-start;
        span {
            width: 24px;
            height: 24px;
            border-radius: 4px;
        }
        span:first-child {
            background-color: #5f3e2d;
            margin-right: 8px;
        }
        span:last-child {
            background-color: #b75929;
        }
    }
    @include pad {
        padding: 0 48px;
        li {
            width: 49%;
            margin-bottom: 72px;
        }
        li:not(:nth-child(4n)) {
            margin-right: 0%;
        }
        li:not(:nth-child(even)) {
            margin-right: 2%;
        }
        p {
            margin-bottom: 0px;
            font-size: 24px;
            line-height: 32px;
        }
    }
    @include mobile {
        padding: 0 12px 0 11px;
        li {
            width: 100%;
            margin-bottom: 32px;
        }
        li:not(:nth-child(even)) {
            margin-right: 0%;
        }
        p {
            font-size: 16px;
            line-height: 30px;
        }
        p:last-child {
            span {
                width: 15px;
                height: 16px;
                border-radius: 4px;
            }
            span:first-child {
                margin-right: 5px;
            }
        }
    }
}
.productsPageNavigation {
    justify-content: center;
    margin-bottom: 156px;
    font-size: 19px;
    li {
        padding: 0px 17px 0px 14px;
        line-height: 48px;
        border-top: 1px solid #555555;
        border-bottom: 1px solid #555555;
        border-left: 1px solid #555555;
        &:hover {
            background-color: #000;
            color: #fff;
        }
        cursor: pointer;
    }
    .active {
        background-color: #000;
        color: #fff;
    }
    li:last-child {
        border-right: 1px solid #555555;
    }

    li:last-child,
    li:first-child {
        padding: 0px 15px 0px 9px;
        &:hover {
            background-color: #f2f2f2;
            color: #000;
        }
    }
    @include pad {
        font-size: 18px;
        margin-bottom: 104px;
    }
    @include mobile {
        font-size: 11px;
        margin-bottom: 56px;
        li {
            padding: 0px 10px 0px 11px;
            line-height: 32px;
        }
        li:last-child,
        li:first-child {
            padding: 0px 8px 0px 9px;
            &:hover {
                background-color: #f2f2f2;
                color: #000;
            }
        }
    }
}
