.blogContainer {
    max-width: 1293px;
    margin: 0 auto;
}
.blogDetail {
    padding: 8px 0 14px 0;
    @include pad {
        padding-left: 48px;
        padding-bottom: 16px;
    }
    @include mobile {
        padding-left: 11px;
        padding-right: 12px;
        padding-bottom: 17.04px;
    }
}

.blogDetailEvent {
    width: 69%;
    margin-right: 24px;
    @include mobile {
        width: 100%;
        margin-right: 0px;
    }
    p:first-child {
        margin-bottom: 16px;
        @include pad {
            margin-bottom: 8px;
        }
    }
    p:nth-child(2) {
        font-size: 24px;
        line-height: 32px;
        @include pad {
            font-size: 16px;
            line-height: 21px;
        }
        @include mobile {
            font-size: 14px;
            line-height: 19px;
        }
    }
    p:nth-child(4) {
        justify-content: space-between;
        font-size: 28px;
        line-height: 37px;
        margin-bottom: 24px;
        font-weight: 600;
        span + span {
            color: #6c6c6c;
            font-size: 16px;
        }
        @include pad {
            font-size: 16px;
            line-height: 21px;
            margin-bottom: 16px;
            span + span {
                font-size: 14px;
            }
        }
        @include mobile {
            font-size: 16px;
            line-height: 21px;
            margin-bottom: 16px;
            span + span {
                font-size: 14px;
            }
        }
    }
    h2 {
        font-size: 48px;
        line-height: 67px;
        margin-bottom: 4px;
        font-weight: 700;
        @include pad {
            font-size: 32px;
            line-height: 45px;
            margin-bottom: 0px;
        }
        @include mobile {
            font-size: 20px;
            line-height: 30px;
            color: #650300;
        }
    }
    .breadcrumbList {
        font-size: 16px;
        line-height: 24px;
        span.active {
            color: #aa0601;
        }
        @include pad {
            font-size: 14px;
        }
    }
    .blogDetailList {
        li {
            margin-bottom: 40px;
        }
        li:last-child {
            margin-bottom: 48px;
            @include pad {
                margin-bottom: 32px;
            }
        }
        img {
            margin-bottom: 24px;
            @include pad {
                margin-bottom: 16px;
            }
        }
        p {
            font-size: 20px;
            line-height: 30px;
            font-weight: 600;
            @include pad {
                font-size: 16px;
                line-height: 24px;
            }
        }
    }
    .share {
        font-weight: 600;
        cursor: pointer;
        img {
            max-width: 24px;
            @include pad {
                max-width: 20px;
            }
            @include mobile {
                max-width: 16px;
            }
        }
        span + img,
        img + img {
            margin-left: 16px;
        }
    }
}
.blogPageNavigation {
    justify-content: space-between;
    width: 69%;
    margin-bottom: 156px;
    font-weight: 600;
    font-size: 19px;
    line-height: 30px;
    @include pad {
        font-size: 16px;
        width: 100%;
        padding-right: 48px;
        margin-bottom: 104px;
    }
    @include mobile {
        font-size: 14px;
        display: block;
        margin-bottom: 40px;
    }
    img {
        max-width: 24px;
        cursor: pointer;
        @include pad {
            max-width: 20px;
        }
        @include mobile {
            max-width: 16px;
        }
    }
    li:first-child {
        img {
            margin-right: 8px;
            vertical-align: text-top;
            @include mobile {
                vertical-align: baseline;
            }
        }
    }
    li:last-child {
        img {
            margin-left: 8px;
            vertical-align: text-top;
            @include mobile {
                vertical-align: baseline;
            }
        }
    }
}
.blogPageNavLayout {
    max-width: 1293px;
    margin: 0 auto;
    hr {
        width: 69%;
        margin-bottom: 24px;
    }
    @include pad {
        padding-left: 48px;
        hr {
            width: 99.8%;
        }
    }
    @include mobile {
        padding-left: 11px;
        padding-right: 36px;
        hr {
            margin-bottom: 8px;
        }
    }
}
.blogDetailTag {
    padding-top: 78px;
    font-weight: 700;
    .tagRight {
        max-width: 48px;
        margin-right: 8px;
    }
    li:first-child {
        align-items: center;
        font-size: 28px;
        line-height: 42px;
    }
    li {
        font-size: 24px;
        line-height: 36px;
    }
    span + span {
        margin-left: 24px;
    }
    @include pad {
        padding-top: 57px;
        .tagRight {
            max-width: 24px;
            margin-right: 0px;
        }
        li:first-child {
            font-size: 20px;
        }
        li {
            font-size: 16px;
            line-height: 21px;
        }
        span + span {
            margin-left: 16px;
        }
    }
    @include mobile {
        display: none;
    }
}
.blogDetailTagMobile {
    padding-top: 0px;
    display: none;
    @include mobile {
        display: block;
    }
    span + span {
        margin-left: 8px;
    }
    margin-bottom: 56px;
}
